
import { defineComponent } from 'vue'
export default defineComponent({
    name: 'BaseSearchInput',
    props: {
        value: {
            type: String,
            required: false,
            default: ''
        },
        modelValue: {
            type: String,
            required: false,
            default: ''
        },
        label: {
            type: String,
            required: false
        },
        type: {
            type: String,
            required: false,
            default: 'text'
        },
        errorMessage: {
            type: String,
            required: false,
            default: ''
        },
        autocomplete: {
            type: String,
            required: false,
            default: ''
        },
        disabled: {
            type: Boolean,
            required: false,
            default: false
        },
        placeholder: {
            type: String,
            required: false,
            default: ''
        },
        loading: {
            type: Boolean,
            required: false,
            default: false
        }
    },
    data() {
        return {
            isActive: false,
            id: null as any
        }
    },
    created(){
        this.id = 'input-' + Math.random()
    },
    methods: {
        activateInput() {
            this.isActive = true
        },
        updateInput(e) {
            this.$emit('update:modelValue', e.target.value)
        }
    },
    watch: {
        modelValue() {
            this.isActive = true
        }   
    }
})
